<template>
  <div class="p-1 p-md-5">
    <h1 class="pl-1 pl-md-5">
      Abgabe für Workshop {{ $route.params.workshopname }}
    </h1>
    <div class="px-1 px-md-5">
      <div class="pt-3">
        <h3 class="d-flex justify-content-start">Angabe:</h3>
        <p>{{ workshop.content }}</p>
        <md-button class="md-raised md-primary">
          <span>Angabe.pdf</span>
          <md-icon>get_app</md-icon>
        </md-button>
      </div>
      <div class="pt-3">
        <h3>Deadline: {{ workshop.end }}</h3>
      </div>
      <div class="pt-3">
        <h3 class="d-flex justify-content-start">Abgabe Datei</h3>
        <form @submit.prevent="validateWorkshop" enctype="multipart/form-data">
          <md-field :class="getValidationClass('title')">
            <label>Titel</label>
            <md-input name="title" v-model="title" :disabled="sending"></md-input>
            <span class="md-error" v-if="!$v.title.required"
              >A title is required</span>
          </md-field>
          <md-field :class="getValidationClass('comment')">
            <label>Kommentar</label>
            <md-textarea name="comment" v-model="comment" :disabled="sending"></md-textarea>
            <span class="md-error" v-if="!$v.comment.required"
              >A comment is required</span>
          </md-field>
          <md-field>
            <!--            <label>Abgabe</label>-->
            <!--            <md-file type="file" acccept=".pdf" ref="file" @change="onFileChange"></md-file>-->
            <!--          <label>Upload File</label><br/>-->
            <input type="file" ref="file" @change="onSelect" />
          </md-field>
          <div
            class="pt-3 d-flex justify-content-center justify-content-md-end"
          >
            <md-button class="md-raised prp-danger" to="/studentdashboard">
              <span class="p-1">Abbrechen</span>
              <md-icon class="prp-danger">delete</md-icon>
            </md-button>
            <md-button class="md-raised prp-success btn_save" type="submit">
              <span class="p-1">Speichern</span>
              <md-icon class="prp-success-icon">done_all</md-icon>
            </md-button>
            <div class="message">
              <h5>{{ message }}</h5>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import DataService from "@/services/DataService";
import AuthHelper from "@/utils/AuthHelper";
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  name: "WorkshopSubmission",
  mixins: [validationMixin],
  data() {
    return {
      workshop: {},
      file: undefined,
      message: "",
      attachment: [],
      title: "",
      comment: ""
    };
  },
  validations: {
    title: {
      required
    },
    comment: {
      required
    }
  },
  methods: {
    onSelect() {
      const file = this.$refs.file.files[0];
      if (file) {
        this.file = file;
      }
      // const allowedTypes = ["image/jpeg", "image/jpg", "image/png"];
      // if(!allowedTypes.includes(file.type)){
      //   this.message = "Filetype is wrong!!"
      // }
      // if (file.size > 500000) {
      //   this.message = "Too large, max size allowed is 500kb";
      // }
    },
    async onSubmit() {
      if (this.file) {
        const formData = new FormData();
        console.log(this.file);
        formData.append("file", this.file);
        try {
          await DataService.postSubmissionFile(
            this.$parent.username,
            this.$parent.pw,
            formData
          )
            .then(res => {
              this.attachment = [res.data.attachment.id];
              this.addSubmission();
            })
            .catch(err => {
              this.message = err;
            });
          this.message = "Uploaded!";
        } catch (err) {
          console.error(err);
          this.message = err;
        }
      } else {
        await this.addSubmission();
      }
    },
    async addSubmission() {
      // Convert object back
      // See: https://github.com/vuejs/Discussion/issues/292
      const attachment = JSON.parse(JSON.stringify(this.attachment));
      try {
        await DataService.addSubmission(
          this.$parent.username,
          this.$parent.pw,
          attachment,
          this.title,
          this.comment,
          this.$route.params.id
        )
          .then(() => {
            // console.log(res.data.ok);
            this.$router.push("/studentdashboard");
          })
          .catch(err => {
            this.message = err;
          });
      } catch (e) {
        alert(e);
        this.message = e;
      }
    },
    getWorkshop() {
      DataService.getStudentWorkshop(
        this.$parent.username,
        this.$parent.pw,
        this.$route.params.id
      )
        .then(response => {
          this.workshop = response.data.workshop;
        })
        .catch(e => {
          console.log(e);
        });
    },
    downloadSubmission() {
      DataService.downloadSubmission(
        this.$parent.username,
        this.$parent.pw,
        this.submission.attachments[0].id
      ).then(response => {
        var fileURL = window.URL.createObjectURL(new Blob([response.data]));
        var fURL = document.createElement("a");
        fURL.href = fileURL;
        fURL.setAttribute("download", "file.pdf");
        document.body.appendChild(fURL);
        fURL.click();
      });
    },
    getValidationClass(fieldName) {
      const field = this.$v[fieldName];
      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    validateWorkshop() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.addSubmission();
      }
    }
  },
  mounted() {
    if (AuthHelper.Authenticated(this)) {
      this.getWorkshop();
    } else {
      AuthHelper.Login(this);
    }
  }
};
</script>

